import React from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext'; //     

const Header: React.FC = () => {
  const { currentUser } = useAuth(); //   

  return (
    <header style={styles.header}>
      <nav style={styles.nav}>
        <Link to="/" style={styles.link}></Link>      {/* <Link to="/video"></Link> */}
        <Link to="/flashcards" style={styles.link}></Link>
        {!currentUser && <Link to="/register" style={styles.link}></Link>} {/*    ,    */}
        {!currentUser && <Link to="/login" style={styles.link}></Link>} {/*    ,    */}
        {currentUser && <Link to="/profile" style={styles.link}></Link>} {/*    ,    */}
        {currentUser?.roleId === 3 && <Link to="/admin" style={styles.link}> </Link>}
        {currentUser?.roleId === 3 && <Link to="/database" style={styles.link}> </Link>}
      </nav>
    </header>
  );
};

const styles: { [key: string]: React.CSSProperties } = {
  header: {
    backgroundColor: '#222',
    padding: '1rem',
    marginBottom: '1rem'
  },
  nav: {
    display: 'flex',
    gap: '1rem',
    justifyContent: 'center'
  },
  link: {
    color: 'black',
    textDecoration: 'none',
    backgroundColor: 'darksalmon',
    padding: '0.5rem',
    borderRadius: '10px'
  }
};


export default Header;
